// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 保养任务service
import ZTree from '@/components/common/ZTree/ZTree';
// 公共方法service
// 文字常量
// 引入table组件
// import VXETable from '@/components/common/Table';
// API
import {
  EQUIPMENT_DATA,
  BTN_TEXT,
  PAGE_TITLE,
  ENERGY,
  MAINTAINTASK_STATUS,
  STATUS_CODE,
  TITLE,
  MSG,
  VARIABLE
  // FILE_TYPE
} from '@/utils/constantsFile';
// API
import {
  // selectFile,
  getAllGroupAndDevice
  // REPAIR_FILE,
  // downloadFile,
  // deleteFileById
} from '@/api/equipment';
import {
  // selectFile,
  getWorkDeviceOfUser,
  getDeviceWorkState,
  startWork,
  stopWork
  // REPAIR_FILE,
  // downloadFile,
  // deleteFileById
} from '@/api/andon';
import { listPlannedProduction } from '@/api/xwReport';
// import { BASE_URL } from '@/utils/ajaxConfigure';
// import { exportMaintainTask } from '@/api/export';
// message service
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 所搜框
import SearchBox from '@/components/common/SearchBox';
// 提示信息
import { showErrorNotify } from '@/utils/notify';
// import moment from 'moment';
export default {
  name: 'MaintainceTask',
  components: {
    ZTree,
    HeaderTitle,
    HeadOperationItem,
    SearchBox
    // VXETable
  },
  inject: ['reload'],
  data() {
    return {
      VARIABLE: VARIABLE,
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      TITLE: TITLE,
      MSG: MSG,
      // 弹窗提示
      warmInfo: '',
      nodeData: [],
      // 查询字段
      // 加载表数据标识
      tableData: [],
      tableLoading: false,
      isCanUpload: true,
      titleText: '新增',
      dialogVisible: false,
      plannedProduction: 0,
      plannedRate: 0,
      cycleTime: 0,
      btnType: 0,
      planRow: {},
      value2: '',
      upDownAcitve: 0, // 0 全部，1 单台
      deviceUpDownData: [],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      }
    };
  },
  methods: {
    /**
     * @description: 获取树结构
     * @return: void
     */
    getTree() {
      getAllGroupAndDevice().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nodeData = res.rows;
          for (var i = 0; i < this.nodeData.length; i++) {
            if (this.nodeData[i].code) {
              this.selectData = this.nodeData[i];
              break;
            }
          }
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    stateFunc(item) {
      if (item.state === 1) {
        return '已上机';
      } else {
        return '可上机';
      }
    },
    stateClass(item) {
      if (item.state === 1) {
        return 'stateUp';
      } else {
        return 'stateDown';
      }
    },
    stateBtnFunc(item) {
      if (item.state === 1) {
        return '下机';
      } else {
        return '上机';
      }
    },
    stateBtnFunc2(item) {
      if (item.state === 1) {
        return '上机';
      } else {
        return '下机';
      }
    },
    getAllDev() {
      this.upDownAcitve = 0;
      this.deviceUpDownData = [];
      getWorkDeviceOfUser({}, {})
        .then(res => {
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            // this.tableData = res.rows;
            // showSuccessMessage('操作成功');
            // this.getFilesData();
            if (res.rows.length === 0) {
              showWarningMessage('当前用户暂无上机设备');
            }

            this.deviceUpDownData = res.rows.map((item) => {
              item.state = 1;
              return item;
            });
          } else {
            showWarningMessage(res.message);
          }
          this.dialogVisible = false;
        });
    },
    getOneDev() {
      this.upDownAcitve = 1;
      this.deviceUpDownData = [];
      getDeviceWorkState({
        deviceCode: this.treeSelectCode
      }, {})
        .then(res => {
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            this.deviceUpDownData = [res.rows];
          } else {
            showWarningMessage(res.message);
          }
          this.dialogVisible = false;
        });
    },
    submitUpDown(item) {
      var that = this;
      var str = '';
      str = that.stateBtnFunc2(item.state);
      if (this.upDownAcitve === 0) {
        str = that.stateBtnFunc2(item.state);
      } else {
        str = that.stateBtnFunc(item.state);
      }

      this.$confirm('此操作将对该设备进行' + str + '操作, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        that.toSubmit(item);
      }).catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: '已取消删除'
        // });
      });
    },
    toSubmit(item) {
      if (item.state === 1) {
        stopWork({
          deviceCode: this.treeSelectCode
        }, {})
          .then(res => {
            if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
              // this.tableData = res.rows;
              showSuccessMessage('操作成功');
              if (this.upDownAcitve === 0) {
                this.getAllDev();
              } else {
                this.getOneDev();
              }
            } else {
              showWarningMessage(res.message);
            }
            this.dialogVisible = false;
          });
      } else {
        startWork({
          deviceCode: this.treeSelectCode
        }, {})
          .then(res => {
            if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
              // this.tableData = res.rows;
              showSuccessMessage('操作成功');
              if (this.upDownAcitve === 0) {
                this.getAllDev();
              } else {
                this.getOneDev();
              }
            } else {
              showWarningMessage(res.message);
            }
            this.dialogVisible = false;
          });
      }
    },
    /**
     * @description: 根据url下载文件
     * @param: {String} url 文件url
     * @return: void
     */
    async update(row) {
      this.planRow = row;

      this.titleText = '修改';
      this.dialogVisible = true;
      this.plannedProduction = row.plannedProduction;
      this.plannedRate = row.plannedRate;
      this.cycleTime = row.cycleTime;
      this.btnType = 1;
    },
    /**
     * @description: 树点击事件
     * @param {object} data 树结构渲染数据
     * @param {object} treeNode 树结构真实数据
     * @return: void
     */
    handleNodeClick(data, treeNode) {
      console.log(treeNode);
      if (treeNode.code) {
        this.treeSelectCode = treeNode.code;
        this.treeSelectGroup = '';
        this.upDownAcitve = 1;
        // this.uploadConfig.data.relationshipId = Number(treeNode.code.split('_')[1]);
        // this.isCanUpload = false;
        // this.$refs.headButtons.showBtn = ['2', '3', '4', '5', '6', '7'];
        // this.selectName = treeNode.code;
        // this.selectCode = treeNode.code;
        // this.showGroup = true;
        // this.toolListTable.page.currentPage = 1;
        // this.toolListTable.page.pageSize = 10;
        // this.getToolListData(treeNode.code);
        this.getOneDev();
      } else {
        this.treeSelectCode = '';
        this.treeSelectGroup = treeNode.id;
        // this.uploadConfig.data.relationshipId = '';
        // this.isCanUpload = true;
        // console.log(treeNode);
        showWarningMessage('请选择设备！');

        // this.treeSelectCode =
        // this.$refs.headButtons.showBtn = ['1', '2', '3', '4', '5', '6', '7', '8'];
        // showWarningMessage(DEVICE_CHECK.PLEASE_CHOOSE_DEVICE);
      }
      // this.getFilesData();
    },
    getFilesData() {
      this.tableData = [];
      listPlannedProduction({
        keyWord: this.treeSelectCode,
        pageNum: 1,
        pageSize: 99999999
      }, {})
        .then(res => {
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            this.tableData = res.rows;
          }
        });
    }
  },
  beforeCreate() {
  },
  computed: {
  },
  mounted() {
  },
  created() {
    this.getTree();
    this.getAllDev();
  }
};
